import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import logo from '../assets/shared/logo.svg';
import '../css/Menu.css';

const Menu = () => {
  const [activeLink, setActiveLink] = useState('Home');

  const handleLinkClick = (linkText) => {
    setActiveLink(linkText);
  };

  return (
    <div className="Menu-container">
      <nav>
        <NavLink exact={true.toString()} to="/project-space/" className="Menu-logo">
          <img src={logo} alt="logo.svg" />
        </NavLink>
        <span className="Menu-line"></span>

        <ul className="Menu">
          <li className={`Menu-link ${activeLink === 'Home' ? 'active' : ''}`}>
            <NavLink exact={true.toString()} to="/project-space/" onClick={() => handleLinkClick('Home')}>
              <span className="Menu-index">00</span> Home
            </NavLink>
          </li>
          <li className={`Menu-link ${activeLink === 'Destination' ? 'active' : ''}`}>
            <NavLink to="/project-space/destination" onClick={() => handleLinkClick('Destination')}>
              <span className="Menu-index">01</span> Destination
            </NavLink>
          </li>
          <li className={`Menu-link ${activeLink === 'Crew' ? 'active' : ''}`}>
            <NavLink to="/project-space/crew" onClick={() => handleLinkClick('Crew')}>
              <span className="Menu-index">02</span> Crew
            </NavLink>
          </li>
          <li className={`Menu-link ${activeLink === 'Technology' ? 'active' : ''}`}>
            <NavLink to="/project-space/technology" onClick={() => handleLinkClick('Technology')}>
              <span className="Menu-index">03</span> Technology
            </NavLink>
          </li>
        </ul>

        <span className="hamburger-icon"></span>
      </nav>
    </div>
  );
};

export default Menu;

